.container-for-animation {
  width: 80%;
  margin: 50px auto;
  overflow: hidden;
  height: 100px; /* Set the desired height for the main container */
}

.container-for-animation2 {
  width: 80%;
  margin: 0.1rem auto;
  overflow: hidden;
  height: 100px; /* Set the desired height for the main container */
}

.image-container {
  /* display: flex;
   */
  display: none;
  justify-content: space-between;
  opacity: 0;
  transition: opacity 1s ease-in-out;
}

.image-container.visible {
  display: flex;
  opacity: 1;
}

.image-container img {
  width: 4rem;
  height: 100%;
  max-height: 100%;
  object-fit: contain;
}
.image-container2 {
  /* display: flex;
   */
  display: none;
  justify-content: space-between;
  opacity: 0;
  transition: opacity 1s ease-in-out;
}

.image-container2.visible {
  display: flex;
  opacity: 1;
}

.image-container2 img {
  width: 4rem;
  height: 100%;
  max-height: 100%;
  object-fit: contain;
}

@media (max-width: 768px) {
  .image-container2 > img,
  .image-container > img {
    width: 2rem;
  }

  .container-for-animation2 {
    margin-top: -2.5rem;
    margin-bottom: 1rem;
  }

  .rds-hero-column-wrapper.w-row {
    margin-top: -1rem;
  }
}
